<script>
import publicFn from '../../../untils/Public.js';
import { mapActions, mapGetters, mapState } from 'vuex'
export default {
  //判斷是否已經登陸
  created: function() {
    if (!localStorage.getItem('access_token')) {
      this.$router.push({ name: "index" })
    }
    this.$store.commit("pushOderChart", null);
    //订单id
    var id = this.$route.query.id;
    this.id = id;
    //
    this.postOderChart(id);
    //患者id
    //协诊单号
    var caseNo = this.$route.query.caseNo;
    this.caseNo = caseNo;
    //医生id
  },
  name: 'Expert',
  data: function() {
    return {
      //s是否存在病历
      nex: false,
      //病历列表
      recode: null,
      //病历图片
      imgList: [
        //              {path:"",id:0},
        //              {imgSrc:"",id:1}
      ],
      //病历 类型下拉框
      setByAuxiliary: [
        { Auxiliary: "心电生理" },
        { Auxiliary: "冠脉血管" },
        { Auxiliary: "高血压" },
        { Auxiliary: "心力衰竭" }
      ],
      //排班
      workingList: [
        {
          date: "",//排班日期
          endTime: "",//排班结束时间
          startTime: "",//排班开始时间
        }
      ],
      recodeShow: false,
      listData: {
        upDataReservation: true,
        list2: {
          bot: false,
          rues: true,
          alerts: true,
          auxiliary: "心电生理",//疾病类型
          ChiefComplaint: "",//  主诉
          MedicalHistoryNow: "",//现病史
          MedicalHistoryOf: "",//既往史
          MedicalHistoryBySelf: "",//个人史
          contagionHistory: "",//传染史
          allergyHistory: "",//过敏史
          familyHistory: "",//家族史
          inspect: "",//检查
          auxiliaryInspect: "",//辅助检查
          imgList: []//影像
        }
      },
      //订单id
      id: null,
      //患者id
      patientId: null,
      //病例号
      caseNo: null,
      //医生id
      doctorId: null,
      //专家id
      expertId: null,
      //是否可以付款
      goPay: false,
      //校验规则
      rules: {
        ChiefComplaint: [
          { required: true, message: '主诉不能为空', trigger: 'blur' },
          { min: 0, max: 50, message: '长度最多为50个字', trigger: 'blur' },
        ],
        MedicalHistoryNow: [
          { required: true, message: '现病史不能为空', trigger: 'blur' },
          { min: 0, max: 500, message: '长度最多为500个字', trigger: 'blur' },
        ],
        MedicalHistoryOf: [
          { min: 0, max: 200, message: '长度最多为200个字', trigger: 'blur' },
        ],
        MedicalHistoryBySelf: [
          { min: 0, max: 200, message: '长度最多为200个字', trigger: 'blur' },
        ],
        contagionHistory: [
          { min: 0, max: 200, message: '长度最多为200个字', trigger: 'blur' },
        ],
        allergyHistory: [
          { min: 0, max: 50, message: '长度最多为50个字', trigger: 'blur' },
        ],
        familyHistory: [
          { min: 0, max: 100, message: '长度最多为100个字', trigger: 'blur' },
        ],
        inspect: [
          { min: 0, max: 1000, message: '长度最多为1000个字', trigger: 'blur' },
        ],
        auxiliaryInspect: [
          { min: 0, max: 1000, message: '长度最多为1000个字', trigger: 'blur' },
        ]
      },
      progressStyle: {
        position: 'absolute',
        left: '50%',
        top: '50%',
        marginLeft: '-50px',
        marginTop: '-50px'
      },//进度条样式
      percentage: [],//进度条进度
      progressIsShow: [],//进度条是否显示
    }
  },
  methods: {
    ...mapActions({
      recodeListFn: 'recodeListFn',
      postOderChart: "postOderChart"
    }),
    //员工详情
    userChaert(id) {
      var url = '/api/patient/patients/' + id;
      var token = localStorage.getItem('access_token');
      this.$http.get(url, {
        headers: {
          Authorization: 'bearer ' + token
        }
      }).then(function(res) {
        //病例号
        this.caseNo = res.body.caseNo;
        //患者id
        this.patientId = res.body.usersId;
        //             //当前时间，最好是服务器时间
        //             var nowDate=publicFn.publicLo.dataChange();
        //             //出生日期
        //             var birthdayDate= publicFn.publicLo.dataChange(res.body.birthday);
        //             //年龄
        //             this.age = parseInt(nowDate.y)-parseInt(birthdayDate.y)+1;
      })
    },
    save1: function(a) {
      if (a == 5) {
        //修改病历
        if (!this.listData.upDataReservation) return false;
        //展示修改病例界面
        this.listData.list2.rues = true;
      }
      this.$refs['listData.list2'].validate((valid) => {
        if (valid) {
          //添加病历
          if (a == 4) {
            this.listData.list2.rues = false;
            //上传图片
            this.postImgs()
          }
        } else {
          document.body.scrollTop = 340;
          return false;
        }
      });
    },
    //取消操作
    clere: function() {
      //有病例id
      if (this.recordId) {
        //展示保存界面
        this.listData.list2.rues = false;
      } else {
        //没有病例id传入取消指令，assistOderIng组件做取消操作;
        this.$store.commit('ucliClereFn', 1);
      }
      return
    },

    postImg: function() {
      //声明变量that 方便回调函数内部使用vue this对象
      var that = this;
      var token = localStorage.getItem('access_token');
      if (this.listData.list2.imgList.length > 19) {
        return
      }
      //图片流上传
      var imgSrc = publicFn.publicLo.senImg("fileByReservation", function(e) {
        var url = '/api/basic/employees/upload';
        //e.target.result ：图片base64格式图片流
        var image = { imageUrl: e.target.result };
        image = JSON.stringify(image);
        var id = publicFn.publicLo.dataChange();
        id = id.time;
        that.listData.list2.imgList.push({
          "createdBy": '',
          "createdWhen": '',
          "deleteFlag": '',
          "fromId": '',
          "fromType": '',
          "id": '',
          "name": '',
          "path": e.target.result,
          "type": '',
          "updatedBy": '',
          "updatedWhen": '',
          move: id
        });
        that.percentage.push(25)
        that.progressIsShow.push(true)
        //第一次上传图片
        that.$http.post(url, image, {
          headers: {
            Authorization: 'bearer ' + token
          },
          emulateJSON: true
        }).then(function(res) {
          // 声明res 代替res.body 以防接口数据格式发生改变
          var res = res.body;
          // 生成为一id（此处用时间戳做为一id）
          // 开始渲染
          // 设置
          that.$set(that.listData.list2.imgList[that.listData.list2.imgList.length - 1], 'path', res.path[0])
          that.$set(that.percentage, that.percentage.length - 1, 100)
          var timer = setTimeout(function() {
            // 设置
            that.$set(that.percentage, that.percentage.length - 1, 25)
            that.$set(that.progressIsShow, that.progressIsShow.length - 1, false)
          }, 1000)
          //清空file内置对象
          document.getElementById('fileByReservation').value = ''
        }).catch(function(res) {
          //上传失败 第二次上传图片
          // that.$http.post(url, image, {
          //   headers: {
          //     Authorization: 'bearer ' + token
          //   },
          //   emulateJSON: true
          // }).then(function(res) {
          //   var res = res.body;
          //   var id = publicFn.publicLo.dataChange();
          //   id = id.time;
          //   that.listData.list2.imgList.push({
          //     "createdBy": '',
          //     "createdWhen": '',
          //     "deleteFlag": '',
          //     "fromId": '',
          //     "fromType": '',
          //     "id": '',
          //     "name": '',
          //     "path": res.path[0],
          //     "type": '',
          //     "updatedBy": '',
          //     "updatedWhen": '',
          //     move: id
          //   });
          //   document.getElementById('fileByReservation').value = ''
          // }).catch(function(res) {

          // });
          that.percentage.splice(that.percentage.length - 1, 1)
          that.progressIsShow.splice(that.progressIsShow.length - 1, 1)
        });
      });
    },
    //删除图片
    delImg: function(id) {
      //删除file内置对象
      document.getElementById('fileByReservation').value = '';
      //声明a变量，用来保存 需要删除对象的index下标
      var a = null;
      for (let i in this.listData.list2.imgList) {
        //循环匹配
        if (this.listData.list2.imgList[i].move == id) {
          a = i//记录删除图片下标
        }
      }
      //删除
      this.listData.list2.imgList.splice(a, 1)
      // this.listData.list2.imgList.splice(a, 1);
      this.percentage.splice(a, 1);
      this.progressIsShow.splice(a, 1)
    },
    //提交病历
    postReservation: function(dat) {
      //从vuex获取当前用户消息
      var user = this.$store.state.user;
      //当前用户id（医生id）
      this.doctorId = user.id;
      //上传图片地址路径
      var path = this.listData.list2.imgList;
      //拼接param
      var parm = {
        "allergyHistory": this.listData.list2.allergyHistory,//过敏史 ,
        "auxiliaryCheck": this.listData.list2.auxiliaryInspect,//辅助检查 ,
        "chiefComplaint": this.listData.list2.ChiefComplaint,//主诉
        "familyHistory": this.listData.list2.familyHistory,// 家庭史
        "history": this.listData.list2.MedicalHistoryNow,//现病史
        "infectionHistory": this.listData.list2.contagionHistory,//传染史 ,
        "pastMedicalHistory": this.listData.list2.MedicalHistoryOf,//既往史
        "personalHistory": this.listData.list2.MedicalHistoryBySelf,//个人史 ,
        "recordCheck": this.listData.list2.inspect,//检查
        "patientId": this.patientId,//患者id
        "caseNo": this.caseNo,//病历号
        "doctorId": this.doctorId,//医生id
        expertId: this.expertId,//专家id
        attachments: path,//影响资料
        id: this.recordId,//病例id（修改时候为0）
        orderId: this.id,//订单id
        age: this.age,//年龄
      };
      var token = localStorage.getItem('access_token');
      var url = '/api/patient/records';
      this.$http.post(url, parm, {
        headers: {
          Authorization: 'bearer ' + token
        }
      }).then(function(res) {
        var res = res.body;
        //保存病例id，方便修改病例
        this.recordId = res.id;
        this.success("资料提交成功");
        //拼接病例信息 传入vuex；方便 assisOderIng组件判断有无上传病例
        var parms = { recordId: this.recordId, description: parm.chiefComplaint };
        this.$store.commit('ucliFn', parms);
      }).catch(function(res) {
        this.warning("病历提交失败，请完病例后重试")
      });
    },
    //病历数据解析
    getRescevation: function(res) {
      res = res;
      if (res[0]) {
        //是否显示选则病例，true：显示，false：不显示
        this.recodeShow = true;
        //保存病例信息
        this.recode = res;
        for (let k in res) {
          for (let i in res[k].attachments) {
            //病例影像单独存储，以便后续操作（暂无）
            this.imgList.push({ path: res[k].attachments[i].path, id: res[k].attachments[i].id })
          }
        }
        this.nex = true;
      }
    },
    //**
    // 选择病历
    // **//
    recodFn: function(list) {
      //重置影像
      this.listData.list2.imgList = [];
      //病例数据复值
      this.listData.list2.allergyHistory = list.allergyHistory;//过敏史 ,
      this.listData.list2.auxiliaryInspect = list.auxiliaryCheck;//辅助检查 ,
      this.listData.list2.ChiefComplaint = list.chiefComplaint;//主诉
      this.listData.list2.familyHistory = list.familyHistory;// 家庭史
      this.listData.list2.MedicalHistoryNow = list.history;//现病史
      this.listData.list2.contagionHistory = list.infectionHistory;//传染史 ,
      this.listData.list2.MedicalHistoryOf = list.pastMedicalHistory;//既往史
      this.listData.list2.MedicalHistoryBySelf = list.personalHistory;//个人史 ,
      this.listData.list2.inspect = list.recordCheck;//检查
      this.recordId = list.id; //病例id
      //          var time2=publicFn.publicLo.dataChange();
      //          var time3=publicFn.publicLo.timeCheng(list.createdWhen);
      //          time2 = time2.time-86400000;
      //影像资料，没有时默认空数组

      this.listData.list2.imgList = list.attachments || [];
      // 进度条
      this.percentage = []
      this.progressIsShow = []
      for (var j = 0; j < this.listData.list2.imgList.length; j++) {
        this.listData.list2.imgList[j]['move']=Math.random()+parseFloat(Math.random())
        this.percentage.push(25)
        this.progressIsShow.push(false)
      }
      //拼接病例信息
      var parm = { recordId: this.recordId, description: list.chiefComplaint };
      this.$store.commit('ucliFn', parm);
    },

    postImgs: function() {
      this.postReservation();
    },
    //
    success: function(text) {
      this.$message({
        message: text,
        type: 'success'
      });
    },
    //查询订单详情
    oderChartFn(res) {
      //保存专家id
      this.expertId = res.expertId;
      //请求病例
      this.recodeListFn(res.usersId);
      //请求患者详情
      this.userChaert(res.patientId);
      this.age = res.age;
    },
    warning: function(text) {
      this.$message({
        message: text,
        type: 'warning'
      });
    },
  },
  mounted: function() {

  },
  computed: {
    //**
    // vuex病例列表
    // **//
    recodeListCom: function() {
      var data = this.recodeList;
      //重置病例信息
      this.recode = null;
      if (data) {
        //解析病例list
        this.getRescevation(data)
      }
      return 1;
    },
    //**
    // vuex订单数据
    // **//
    oderChartState: function() {
      var data = this.oderChart;
      if (data) {
        //解析订单
        this.oderChartFn(data)
      }
      return 1;
    },
    ...mapState([
      'recodeList',
      'oderChart'
    ]),
  },
}
</script>



