        return (restaurant.value.indexOf(queryString.toLowerCase()) === 0);
      };
    },
    //患者选择
    handleSelect: function(item) {
      this.user.six = item.sex;
      this.user.inputFrendName = item.emergencyContact;
      this.user.inputIdCard = item.idNumber;
      this.user.inputPhone = item.euMobile;
      this.user.inputPhonebyfrend = item.emergencyMobile;
      this.inputAgeText = publicFn.publicLo.timeCheng(item.birthday);
      this.inputAgeText = this.inputAgeText.ymd;
      this.inputAge=item.ageText+'岁';
      var birthday = publicFn.publicLo.timeCheng(item.birthday);
      birthday = birthday.ymd;
      this.user.inputAge = new Date(birthday);
      this.listData.list1.rues = false;
      this.cler = true;
      this.pantionId = item.usersId;
      this.hospitalId = item.hospitalId;
      this.caseNo = item.caseNo;
      this.$refs['user'].validate()
    },
    //保存
    save1: function(a) {
      //编辑资料
      if (a == 1) {
        this.$refs['user'].validate((valid) => {
          if (valid) {
            this.postPatient();
          } else {
            return;
          }
        });
      }
      if (a == 2) {
        this.listData.list1.rues = true;
        this.listData.list2.bot = false;
        this.user.inputAge = this.age;
        this.cler = false;
      }
      //+编辑患者资料按钮
      if (a == 3) {
        this.listData.list1.alerts = false;
      }
      if (a == 4) {
        this.listData.list2.rues = false;
      }
      if (a == 5) {
        this.listData.list2.rues = true;
      }
      if (a == 6) {
        if (this.listData.list2.bot) {
          this.listData.list2.alerts = false;
        } else {
          return false
        }
      }
      //取消按钮
      if (a == 7) {
        if(this.pantionId){
          //隐藏form表单
          this.listData.list1.rues = false;
          //显示编+辑患者按钮
          this.listData.list2.bot = true;
          //不显示清空按钮
          this.cler = false;
        }else {
          this.listData.list1.alerts = true;
          this.listData.list2.bot = false;
          this.listData.list2.rues = true;
          this.user = {
            state2: "",
            inputName: "",
            six: 1,
            inputAge: "",
            inputFrendName: "",
            inputIdCard: "",
            inputPhone: "",
            inputPhonebyfrend: ""
          }
          this.pantionId = 0;
          this.hospitalId = null;
          this.caseNo = null;
          this.cler = false;
          this.inputAge=null;
        }

      }
      if (a == 8) {
        this.listData.list1.alerts = true;
        this.listData.list2.bot = false
      }
    },
    //选择性别
    sixs: function(six) {
      this.listData.list1.six = six;
      this.verification.sixText = "";
    },
    postImg: function(domId) {
      //          var file=document.getElementById(domId).files[0];
