    },
    //添加患者
    postPatient: function(dat) {
      if (this.errorText) {
        this.warning(this.errorText);
        return
      }
      var token = localStorage.getItem('access_token');
      var list = this.user;
      var parme = {
        account: null,
        address: null,
        birthday: list.inputAge,
        caseNo: "",
        "chineseName": list.state2,
        "emergencyContact": list.inputFrendName,
        "emergencyMobile": list.inputPhonebyfrend,
        "euMobile": list.inputPhone,
        "idNumber": list.inputIdCard,
        "sex": list.six,
        "userType": 0,
        "id": this.id,
        usersId: this.pantionId,
        hospitalId: this.hospitalId,
      };
      var sus = parme.usersId;
      parme = JSON.stringify(parme);
      var url = '/api/patient/patients';
      this.$http.post(url, parme, {
        headers: {
          Authorization: 'bearer' + token
        },
        emulateJSON: true
      }).then(function(res) {
        res = res.body;
        this.age = this.user.inputAge
        this.user.inputAge = publicFn.publicLo.timeCheng(res.birthday);
        this.user.inputAge = this.user.inputAge.ymd;
        this.inputAgeText = this.user.inputAge;
        this.pantionId = res.usersId;
        this.hospitalId = res.hospitalId;
        this.caseNo = res.caseNo;
        this.id = res.id;
        if(res.age){
          this.inputAge=res.age+'岁';
        }
        if(!res.age){
          this.patientChart(res.id)
        }
        if (!sus) {
          this.success("添加成功")
        } else {
          this.success("保存成功")
        }
        this.listData.list1.rues = false;
        this.listData.list2.bot = true;

      }).catch(function(res) {
        var message2 = res.body.message.split(";");
        message2[1] = message2[1].substring(9, message2[1].length);
        message2[1] = JSON.parse(message2[1]);
        this.warning(message2[1].message)
      })
    },
    //排班
    working: function() {
      //organization/assistSchedules
      //        createdWhen   创建时间 ,
      //        date : 排班日期 ,
      //        employeeId : 医生ID ,
      //        endTime : 排班结束时间 ,
      //        id : 主键 ,
      //        startTime : 排班开始时间 ,
      //        totalNum : 总放号量 ,
      //        usedNum : 已预约放号量
      var token = localStorage.getItem('access_token');
      var parm = {
        date: this.thisDate,
        employeeId: this.xiezhenId
      }
      // var url = '/api/organization/assistSchedules?employeeId='+ExpertId
      var url = '/api/organization/assistSchedules';
      this.$http.get(url, {
        headers: {
          Authorization: 'bearer ' + token
        },
        params: parm
      }).then(function(res) {
        res = res.body;
        this.Scheduleslist = [];
        var lis = {};
        var dateTime = new Date();
        dateTime = publicFn.publicLo.dataChange(dateTime);
        for (let i in res) {
          if (res[i].usedNum < res[i].totalNum && dateTime.time <= res[i].endTime) {
            var time = publicFn.publicLo.timeCheng(res[i].startTime);
            var date = publicFn.publicLo.timeCheng(res[i].date);
            var endTime = publicFn.publicLo.timeCheng(res[i].endTime);
            var over = parseInt(res.totalNum) - parseInt(res[i].usedNum);
            lis = {
              scheduleId: res[i].id,
              startTime: time.hms,
              date: date.ymd,
              week: date.week,
              endTime: endTime.hms,
              over: over,
              totalNum: res[i].totalNum,
              usedNum: res[i].usedNum,
              activ: false
            };
            //
            var pus = this.matchingTime(this.thisDate, lis.date);
            if (pus) {
              this.Scheduleslist.push(lis);
              this.Schedules.date = lis.date;
              this.Schedules.week = lis.week;
            }
          }
        };
      });
    },
    //选择协诊类型
    tyepWay: function(a) {
      for (let i in this.orderType) {
        this.orderType[i].status = 1;
      }
      a.status = 0;
      this.choiceorderType.type = a.type;
      this.choiceorderType.pric = a.price;
      this.choiceorderType.id = a.orderTypeId;
    },
    //日历插件
    rili: function() {
      $('input[name=datetime]').daterangepicker({
        singleDatePicker: true,
        showOn: 'both',
        locale: {
          daysOfWeek: ["日", "一", "二", "三", "四", "五", "六"],
          monthNames: ["一月", "二月", "三月", "四月", "五月", "六月", "七月", "八月", "九月", "十月", "十一月", "十二月"],
        }
      }, function(start, end, label) {

      })
    },
    //校验唯一数据
    xiaoyan: function(parm) {
      var parms = {};
      this.errorText = 0;
      var url = "/api/patient/patients/exist";
      //校验身份证
      if (parm == "inputIdCard") {
        parms.idNumber = this.user.inputIdCard;
        this.getxiaoyan(parms, url, "身份证号码已存在");
      }
      //校验手机
      if (parm == "inputPhone") {
        parms.phone = this.user.inputPhone;
        this.getxiaoyan(parms, url, "手机号码已存在");
      }
    },
    //get 校验数据
    getxiaoyan(parm, url, text) {
      this.$http.get(url, {
        headers: {
          Authorization: 'bearer ' + this.token
        },
        params: parm
      }).then(function(res) {
        res = res.body;
      }).catch(function(res) {
        this.$message({
          message: res.body.message,
          type: 'warning'
        })
        this.errorText = res.body.message
      })
    },
    //输入框列表
    patientList: function(hospitalId) {
      var parme = { isAll: true, hospitalId: hospitalId, pageNumber: 1, pageSize: 1000 };
      var url = '/api/patient/patients';
      var token = localStorage.getItem('access_token');
      this.$http.get(url, {
        headers: {
          Authorization: 'bearer ' + token
        },
        params: parme
      }).then(function(res) {
        res = res.body.list;
        this.inputAlertBlock = true;
        this.patientListData = [];
        var dat = [];
        for (let i in res) {
          //带入的患者信息
          var date = {
            chineseName: res[i].chineseName,
            emergencyContact: res[i].emergencyContact, // 紧急联系人 ,
            emergencyMobile: res[i].emergencyMobile, // 紧急联系人电话 ,
            euMobile: res[i].euMobile,//电话 ,
            id: res[i].id,// 患者id ,
            idNumber: res[i].idNumber,//身份证号码
            sex: res[i].sex,//性别
            age: res[i].birthday, //年龄
            ageText:res[i].age,
            hospitalId: res[i].hospitalId,//
            usersId: res[i].usersId,
            value: res[i].chineseName,
            caseNo: res[i].caseNo,
            birthday: res[i].birthday
          }
          if (this.$route.query.patientId == res[i].id) {
            this.listData.list1.alerts = false;
            this.user.state2 = res[i].chineseName;
            this.handleSelect(date);
          }
          this.patientListData.push(date);
          dat.push(date)
        }
        this.restaurants = dat;
      });
    },
    //患者详情
    patientChart(id){
      var url = '/api/patient/patients/'+id;
      var token = localStorage.getItem('access_token');
      this.$http.get(url, {
        headers: {
          Authorization: 'bearer ' + token
        }
      }).then(function(res) {
        this.inputAge=res.body.age+"岁";
