    //下单
    CreteOder: function() {
      var user = this.$store.state.user;
      this.doctorId = user.id;
      if (!this.Schedules.id) {
        this.warning("选择预约时间");
        return
      }
      if (!this.diseaseType.id) {
        this.warning("选择疾病类型");
