      }
      if (!this.choiceorderType.id) {
        this.warning("选择协诊方式");
        return
      }
      if (!this.pantionId || this.listData.list1.rues) {
        this.warning("请填写患者资料");
        return
      }
      var assistOrder = {
        "scheduleId": this.Schedules.id,//排班id
        "doctorId": this.doctorId,//医生id
        "expertId": parseInt(this.xiezhenId),//专家id
        "totalPrice": this.choiceorderType.pric,//订单价格
        "type": this.choiceorderType.type,//面诊类型
