        "recordType": parseInt(this.diseaseType.id),
        "hospitalId": this.hospitalId,
        "skuId": this.choiceorderType.id
      }
      var token = localStorage.getItem('access_token');
      assistOrder = JSON.stringify(assistOrder);
      var url = '/api/assist-order/assistOrders';
      this.$http.post(url, assistOrder, {
        headers: {
          Authorization: 'bearer' + token
        },
        emulateJSON: true
      }).then(function(res) {
        res = res.body;
